.leaflet-container {
    width: 100%;
    height: 820px;
    background-color:rgba(255,0,0,0.0);
  }
  
  .info {
    padding: 6px 8px;
    font: 14px/16px sans-serif;
    background: white;
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    min-width: 140px;
  }
  .info h4 {
    margin: 0 0 5px;
    color: #777;
  }
  .legend {
    text-align: left;
    line-height: 18px;
    color: #555;
  }
  .legend i {
    width: 18px;
    height: 18px;
    float: left;
    margin-right: 8px;
    opacity: 0.7;
    clear: both; 
  }
  
  .legend div {
    padding: 1px;
  }